/* OCULTANDO CABEÇALHO E RODAPÉ PARA IMPRESSÃO */
@media print {
  #Header,
  #Footer {
    display: none !important;
  }
}

/* CONTAINERS */
body {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  border-radius: 0px;
  margin: 0px;
  padding: 5px;
  font-size: 14px;
  cursor: default;
}

.secondary {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  margin-top: 5px;
  margin-bottom: 5px;
  margin-left: 5px;
  margin-right: 5px;
  cursor: default;
  box-shadow: 0 0 0.3em grey;
}

.menucover {
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 0;
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  margin: 0;
  z-index: 1;
}

.menuposition {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: transparent;
  align-items: center;
  border-radius: 5px;
  padding: 0px;
  cursor: default;
  z-index: 2;
}

.menucontainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #ffffff;
  align-items: center;
  border-radius: 5px;
  padding: 20px;
  cursor: default;
  z-index: 2;
  width: auto;
}
@keyframes test {
  0% {
    max-width: 0px;
    max-height: 0px;
  }
  100% {
    max-width: 1000px;
    max-height: 1000px;
  }
}

.menucontainer1 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #ffffff;
  align-items: center;
  border-radius: 5px;
  padding: 20px;
  cursor: default;
  z-index: 2;
  width: auto;
  animation-name: test;
  animation-duration: 1s;
}
@keyframes test {
  0% {
    max-width: 0px;
    max-height: 0px;
  }
  100% {
    max-width: 1000px;
    max-height: 1000px;
  }
}

