/* INPUTS E TEXTAREAS */
.input {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  color: #1f7a8c;
  font-weight: bold;
  background-color: #e1e5f2;
  border: 0px;
  border-color: transparent;
  border-radius: 5px;
  outline: 0px;
  box-shadow: transparent;
  margin: 5px;
  height: 50px;
  min-height: 50px;
  cursor: default;
  box-shadow: 0 0 0.3em grey;
  text-shadow: 0.5px 0.5px 5px grey;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.textarea {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 300px;
  height: 85px;
  resize: none;
  padding: 10px;
  text-align: left;
  text-transform: uppercase;
  color: #1f7a8c;
  font-weight: bold;
  background-color: #e1e5f2;
  border-width: 5px;
  border-color: #e1e5f2;
  border-radius: 5px;
  outline: 0px;
  box-shadow: transparent;
  margin: 5px;
  cursor: default;
  box-shadow: 0 0 0.3em grey;
  text-shadow: 0.5px 0.5px 5px grey;
}

.textarea::-webkit-scrollbar {
  width: 20px;
}

.textarea::-webkit-scrollbar-track {
  background-color: #bfdbf7;
  border-radius: 5px;
  box-shadow: inset 0 0 0.3em grey;
}

.textarea::-webkit-scrollbar-button {
  display: none;
}

.textarea::-webkit-scrollbar-corner {
  display: none;
}

.textarea::-webkit-scrollbar-thumb {
  background-color: #1f7a8c;
  border-radius: 5px;
  box-shadow: 0 0 0.3em grey;
}

.textarea::-webkit-scrollbar-thumb:hover {
  background-color: #279ab1;
  border-radius: 5px;
}

