/* ANIMAÇÕES */
.fade-in {
  animation-name: fadein;
  animation-duration: 0.5s;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.fade-out {
  animation-name: fadeout;
  animation-duration: 0.5s;
  animation-delay: 2s;
}

@keyframes fadeout {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.pulsar-in {
  animation-name: pulsarin;
  animation-duration: 5s;
}

@keyframes pulsarin {
  0% {
    opacity: 0;
  }
  70% {
    opacity: 0;
  }
  100% {
    opacity: 100;
  }
}

.slide {
  animation: slide 8s forwards;
}

@keyframes slide {
  0% {
    left: 0px;
  }
  100% {
    left: -50px;
  }
}

.pacientes-menu-in {
  left: 0px;
  transition: 0.5s;
}

.pacientes-menu-stay {
  left: 0px;
}

.pacientes-menu-out {
  left: -300px;
  transition: 0.5s;
}

.body-animation {
  animation-name: bodyanimation;
  animation-duration: 3s;
}

@keyframes bodyanimation {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes cardanimation {
  from {
    background-color: "#E1E5F2";
    border-color: "#E1E5F2";
  }
  to {
    background-color: "#BFDBF7";
    border-color: "#BFDBF7";
  }
}

.grow {
  animation-name: grow;
  animation-duration: 1000ms;
  animation-delay: 0s;
}

.growplus {
  animation-name: growplus;
  animation-duration: 1000ms;
  animation-delay: 0s;
}

.growminus {
  animation-name: growminus;
  animation-duration: 1000ms;
  animation-delay: 0s;
}

@keyframes grow {
  from {
    opacity: 0;
    max-width: 0px;
    max-height: 0px;
  }
  to {
    opacity: 1;
    max-width: 1000px;
    max-height: 1000px;
  }
}

@keyframes growplus {
  from {
    max-width: 800px;
    max-height: 800px;
  }
  to {
    max-width: 1000px;
    max-height: 1000px;
  }
}

@keyframes growminus {
  from {
    max-width: 1000px;
    max-height: 1000px;
  }
  to {
    max-width: 800px;
    max-height: 800px;
  }
}

/* RADIO */
.radio-toolbar input[type="radio"] {
  opacity: 0;
  position: fixed;
  width: 0;
  cursor: default;
}

.radio-toolbar label {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #ffffff;
  font-weight: bold;
  min-width: 50px;
  min-height: 50px;
  height: 50px;
  width: 100px;
  background-color: #1f7a8c;
  border: 0px;
  border-color: transparent;
  border-radius: 5px;
  outline: 0px;
  box-shadow: transparent;
  padding-left: 10px;
  padding-right: 10px;
  margin-top: 0px;
  margin-bottom: 5px;
  margin-left: 2.5px;
  margin-right: 2.5px;
  cursor: default;
}

.radio-toolbar input[type="radio"]:focus + label {
  background-color: #1f7a8c;
  cursor: default;
}

.radio-toolbar input[type="radio"]:checked + label {
  background-color: #ec7063;
  cursor: default;
}

