  background-color: transparent;
  border: 0px;
  border-color: transparent;
  border-radius: 5px;
  outline: 0px;
  box-shadow: transparent;
  margin: 2.5px;
  padding: 0px;
  cursor: default;
  text-shadow: 1px 1px 3px grey;
}

/* TOAST */
.toast {
  animation: fadeinout 3s ease-in-out forwards;
}

@keyframes fadeinout {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.linegroup {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 800px) {
  .linegroup {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .smallgone {
    visibility: hidden;
  }
}
